import React, {useEffect} from 'react';
import './header.scss';
import Grid from './Grid';
import { BsFillGridFill } from 'react-icons/bs';
import { FaGripHorizontal } from 'react-icons/fa';
import { BiDotsHorizontalRounded } from 'react-icons/bi';
import { Link } from 'react-router-dom';
import { headerAnim } from "../animations"


function Header({show}) {

    useEffect(() => {
        headerAnim()
      }, [])


    return (
   
        <div  className="header">
            
            
         <div className="container">
             <div className="header-container" >
                 <div className="header-text">Welcome to Novela, the </div>   
            </div>
            <div className="header-container">
                <div className="header-text">simplest way to start</div>    
            </div>
            <div className="header-container">
                <div className="header-text">publishing with Gatsby.</div>   
            </div>
         </div>

         
            

            <div className="header-bottom">
                <div className="header-left">
                    <div className={show ? "header-image border-light" : "header-image border-dark"}>
                        <img src="./images/IMG_3990.jpg" alt="header-img"/>
                    </div>

                    <Link to="/article-page">
                    <div className="header-b-text">
                         Written by <b>You</b>. This is where your author bio lives. Share your work, your joys and of course, your <span>awesome Twitter handle</span>.
                    </div>
                    </Link>
                </div>

                <div className="header-right">
                    <Link to="/">
                        <div className={show ? "logo logo-left light-left" : "logo logo-left dark-left"}>< BsFillGridFill size="1.1em" /></div>
                    </Link>
                    <Link to="/column">
                        <div className={show ? "logo logo-right" : "logo dark-right"}><  FaGripHorizontal size="1.2em" /></div>
                    </Link>
                    
                    
                </div>

            </div>

       
             <Grid show={show} className="grid" />
        
    

        <div className="page-list">
            <div className={show ? "list first page-light" : "list first page-dark"}>1</div>
            <div className="list">2</div>
            <div className="list">3</div>
            <div className="list icon">< BiDotsHorizontalRounded size="1em" /></div>
            <div className="list">12</div>
            <div className={show ? "list last page-light" : "list last page-dark"}>Next</div>
        </div>
            
        </div>
      
        
    )
}

export default Header;
