import React from 'react'
import './close.scss';
import { MdClose } from 'react-icons/md';
import { Link } from 'react-router-dom';

function Close({show}) {
    return (
        
        <div className="close">
           <Link to="/"> 
           <div className={show ? "close-link" : "close-link-dark"}>< MdClose size="1.3em" /></div> 
             </Link>  
        </div>
        
    )
}

export default Close;