import React, { useState, useRef, useEffect } from 'react';
import './App.scss';
import Nav from './components/Nav';
import Header from './components/Header';
import Footer from './components/Footer';
import Column from './components/Column';
import Article from './components/Article';
import ArticlePage from './components/ArticlePage';
import {HashRouter as Router, Switch, Route} from 'react-router-dom';
import styled, { ThemeProvider } from 'styled-components';
import { lightTheme, darkTheme, GlobalStyles } from './themes.js';


const StyledApp = styled.div`
   color: ${(props) => props.theme.fontColor};
   transition: all 1s ease-in;  

`;

const StyledWrapper = styled.div`

width: 100vw;
height: 100vh;
margin: 0;
background: #000;

   .container {
    position: absolute;
    left: 0;
    right: 0;
    margin: 0;
    padding: 0;
    height: 0;
    width: 0;
    background: #fff;
    z-index: 10;
    overflow: hidden;
}

`;

function App() {

const [theme, setTheme] = useState("light");
const [show, setShow]=useState(true);


const changeColor = (theme) => {
      theme === "light" ? setTheme("dark") : setTheme("light");
      setShow(!show)
}



  return (
    <ThemeProvider theme={theme === "light" ? lightTheme : darkTheme}>
    <Router>
    <GlobalStyles />
    <StyledApp>
    <Nav changeColor={changeColor} theme={theme} show={show}/>
    <Switch>
        <Route path="/" exact render={() => <Header show={show}/>}/>
        <Route path="/column" exact render={() => <Column show={show}/>}/>
        <Route path="/article" exact render={() => <Article changeColor={changeColor} theme={theme} show={show}/>}/>
        <Route path="/article-page" exact render={() => <ArticlePage show={show}/>}/>
    </Switch>
    <hr/>
    <Footer />
    </StyledApp>
    </Router>
    </ThemeProvider>
  );
}

export default App;
