import gsap from "gsap"
import { ScrollTrigger } from "gsap/all"

gsap.registerPlugin(ScrollTrigger); //register scroll trigger plugin


export const headerAnim = () => {

    let tl = gsap.timeline()
    let nav = document.querySelectorAll(".nav")
    let headBottom = document.querySelectorAll(".header-bottom")
    let headText = document.querySelectorAll(".header-text")

tl.from(nav, {
    duration: 0.6,
    opacity: 0,
     y: -100,
    ease: "back.easeOut",
  }).from(headText, {
    duration: 1,
    opacity: 0,
    x: 100,
    ease: "Expo.easeOut",
    stagger: {
      amount: 0.5,
    }
  }).from(headBottom, {
    duration: 0.4,
    opacity: 0,
     y: 100,
    ease: "Expo.easeOut",
  })
}


export const imageAnim = () => {

    gsap.utils.toArray("#sections").forEach(anim => {
        gsap.from(anim, {
          scrollTrigger: {
            trigger: anim,
            start: "top bottom",
            toggleActions: "play none none none",
          },
          duration: 1,
          y: 100,
          opacity: 0,
          transformOrigin: "center center",
          ease: "Power3.easeOut",
        })
      })
    
    gsap.utils.toArray(".reveal").forEach(container => {
        let image = container.querySelector(".image")          
        let tl = gsap.timeline({
          scrollTrigger: {
            trigger: container,
            toggleActions: "play none none none",
          },
        })
    
        tl.set(container, { autoAlpha: 1 })
      
       tl.from(image, {
          duration: 1.5,
           y: 100,
           scale: 1.5,
           ease: "Expo.easeOut"
       })
      })
  
  
  }

  export const articleAnim = () => {
      

    let tl = gsap.timeline()
    let content = document.querySelectorAll(".content")
    let articleText = document.querySelectorAll(".article-anim")
    let articleImage = document.querySelector(".article-image") 

tl.from(articleText, {
    duration: 1.5,
    opacity: 0,
    x: -100,
    ease: "Expo.easeOut",
    stagger: {
      amount: 0.5,
    }
  }).from(content, {
    duration: 1,
    opacity: 0,
     x: -100,
    ease: "Expo.easeOut",
  }).from(articleImage, {
    duration: 1,
     opacity: 0,
     scale: 1.2,
     ease: "Expo.easeOut"
 })
}

