import React from 'react'
import './back.scss';
import { IoIosArrowBack } from 'react-icons/io';
import { Link } from 'react-router-dom';

function Back({show}) {
    return (
        
        <div>
           <Link to="/"> 
           <div className={show ? "back back-light" : "back back-dark"}>< IoIosArrowBack size="0.8em" /></div>
             </Link>  
        </div>
        
    )
}

export default Back;
