import React from 'react'
import './nav.scss';
import { IoLogoPolymer } from 'react-icons/io';
import { VscLink } from 'react-icons/vsc';
import { BsMoon } from 'react-icons/bs';
import { BiSun } from 'react-icons/bi';

function Nav({ show, theme, changeColor}) {


    return (
        <div className="nav">

            <div className="nav-left">

            <div className={show ? "logo" : "dark-logo"}>< IoLogoPolymer size="2.1em" /></div>
              <div className={show ? "narative-div light-nar" : "narative-div dark-nar"}>narative</div>
              <div className="themes-div">themes</div>   
            </div>
            
            

            <div className="nav-right">

            <div className="logo link">< VscLink size="1.3em" /></div>
            <div className={show ? "active" : "logo mode"} onClick={() => changeColor(theme)}>< BiSun size="1.3em" /></div>
            <div className={show ? "logo mode" : "active"} onClick={() => changeColor(theme)}>< BsMoon size="1.3em" /></div>

            </div>

            
        </div>
    )
}

export default Nav
