import React from 'react';
import SectionOne from './SectionOne';
import SectionTwo from './SectionTwo';
import SectionThree from './SectionThree';

function Grid({show}) {


    return (
        <div>
            <SectionOne show={show}/>
            <SectionTwo show={show}/>
            <SectionThree show={show}/>
        </div>
    )
}

export default Grid
