import React from 'react';
import './footer.scss';
import { FaYoutube } from 'react-icons/fa';
import { FaGithub } from 'react-icons/fa';
import { FaDribbble } from 'react-icons/fa';
import { FaTwitter } from 'react-icons/fa';
import { FaInstagram } from 'react-icons/fa';
import { FaLinkedinIn } from 'react-icons/fa';

function Footer() {
    return (
        <div className="footer">
            
            <div className="copyright">
               © 2019 Built with <span>Gatsby</span>, designed by <span>Narative</span> 
            </div>

            <div className="socials">
                    <div className="first">< FaYoutube size="1em" /></div>
                    <div className="logo">< FaGithub size="1em" /></div>
                    <div className="logo">< FaDribbble size="1em" /></div>
                    <div className="logo">< FaTwitter size="1em" /></div>
                    <div className="logo">< FaInstagram size="1em" /></div>
                    <div className="logo">< FaLinkedinIn size="1em" /></div>
            </div>
        </div>
    )
}

export default Footer
